import { useState } from 'react';
import { AnimationOnScroll } from 'react-animation-on-scroll';
import {
  LoadingStyled,
  LayoutStyled,
  TitleStyled,
  CardStyled,
  CardTokenStyled,
  CardTitleStyled,
  CardDescStyled,
  CardBorderStyled,
  CardContentStyled,
  CardBorderBgStyled,
  TokenCardStyled,
  TokenCardTextStyled,
  TokenCardBgStyled,
  LeaderCardStyled,
  LinkStyled,
  BgStyled
} from './styled';

import LogoImg from '../../images/logo.svg';
import TitleBgImg from '../../images/titleBg.png';
import TitlePointImg from '../../images/titlePoint.svg';

import BuidlBorderImg from '../../images/buidl/border.svg';
import BuidlBorderShadowImg from '../../images/buidl/borderShadow.png';
import BuidlBorderBgImg from '../../images/buidl/borderBottom.png';
import BuidlBorderArrowImg from '../../images/buidl/borderArrow.svg';
import BuidlArrorImg from '../../images/buidl/arrow.png';
import BuidlTokenImg from '../../images/buidl/tokenBg.png';

import HodlTextBgImg from '../../images/hodl/tokenText.png';
import HodlBgImg from '../../images/hodl/tokenBg.png';
import HodlFrontImg from '../../images/hodl/tokenFront.png';

export function anim(props) {
  return {
    ...props,
    animateIn: props.animateIn || 'animate__fadeInUp',
    delay: props.delay || 0,
    animateOnce: props.animateOnce === undefined ? true : props.animateOnce
    // animateOnce: false
  };
}

export function Loading() {
  return (
    <LoadingStyled>
      <img src={LogoImg} alt="" />
    </LoadingStyled>
  );
}

export function Layout(props) {
  const { children } = props;

  return (
    <LayoutStyled>
      <div className="layout">{children}</div>
    </LayoutStyled>
  );
}

export function Title(props) {
  const { children, min } = props;

  return (
    <AnimationOnScroll {...anim(props)}>
      <TitleStyled className={min ? 'min' : ''}>
        <img src={TitleBgImg} alt="" />
        <div className="title-wrapper">
          <div className="title-content">
            <img src={TitlePointImg} alt="" />
            <div>{children}</div>
            <img src={TitlePointImg} alt="" />
          </div>
        </div>
      </TitleStyled>
    </AnimationOnScroll>
  );
}

export function Link(props) {
  const { className, href, target, children } = props;

  return (
    <LinkStyled className={className} href={href} target={target || '_blank'} rel={target ? '' : 'noopener noreferrer'}>
      {children}
    </LinkStyled>
  );
}

export function Background(props) {
  const { className, src } = props;

  return <BgStyled className={className} src={src} alt="" />;
}

export function Card(props) {
  const { token, title, desc, url } = props;
  const [hover, setHover] = useState(false);

  return (
    <CardStyled
      onMouseEnter={() => {
        setHover(true);
      }}
      onMouseLeave={() => {
        setHover(false);
      }}
    >
      <AnimationOnScroll {...anim(props)}>
        <Link href={url} target={title}>
          <CardBorderStyled border={BuidlBorderShadowImg} borderW={43} />
          <CardBorderBgStyled bg={BuidlBorderBgImg} />
          <CardBorderStyled hover={hover} border={BuidlBorderImg}>
            <img src={BuidlBorderArrowImg} alt="" />
            <img src={BuidlBorderArrowImg} alt="" />
          </CardBorderStyled>
          <CardTokenStyled bg={BuidlTokenImg}>
            <img src={token} alt="" />
          </CardTokenStyled>
          <CardContentStyled>
            <CardTitleStyled arrow={BuidlArrorImg}>
              <div>{title}</div>
            </CardTitleStyled>
            <CardDescStyled>{desc}</CardDescStyled>
          </CardContentStyled>
        </Link>
      </AnimationOnScroll>
    </CardStyled>
  );
}

export function TokenCard(props) {
  const { token, symbol, name, url } = props;
  const [hover, setHover] = useState(false);

  return (
    <TokenCardStyled
      onMouseEnter={() => {
        setHover(true);
      }}
      onMouseLeave={() => {
        setHover(false);
      }}
    >
      <Link href={url} target={name}>
        <AnimationOnScroll {...anim(props)}>
          <TokenCardTextStyled bg={HodlTextBgImg} hover={hover}>
            <div className="token-card-bg" />
            <div className="token-card-symbol">{symbol}</div>
            <div className="token-card-name">{name}</div>
          </TokenCardTextStyled>
          <TokenCardBgStyled>
            <img src={HodlBgImg} alt="" />
            <img src={token} alt="" />
            <img src={HodlFrontImg} alt="" />
          </TokenCardBgStyled>
        </AnimationOnScroll>
      </Link>
    </TokenCardStyled>
  );
}

export function LeaderCard(props) {
  const { img, lightImg, name, url } = props;
  const [hover, setHover] = useState(false);

  return (
    <LeaderCardStyled>
      <AnimationOnScroll {...anim(props)}>
        <img className="leader-card-img" src={img} alt="" />
        <img className={`leader-card-img-hover ${hover ? 'show' : ''}`} src={lightImg} alt="" />
        <div
          className="leader-card-mask"
          onMouseEnter={() => {
            setHover(true);
          }}
          onMouseLeave={() => {
            setHover(false);
          }}
        >
          <Link className={`${hover ? 'show' : ''}`} href={url} target={name}>
            {name}
          </Link>
        </div>
      </AnimationOnScroll>
    </LeaderCardStyled>
  );
}
